#tech {
    margin: auto;
    border-radius: 15px;
    font-size: 45px;
    margin-bottom: 30px;
  }
  #tech h1 {
    text-align: center;
    margin-bottom: 5px;
    color:var(--color-second)
  }
  
    #skillsdiv {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      gap: 30px;
      margin: auto;
  
      width: 80%;
      border-radius: 20px;
  
      align-self: center;
      position: relative;
    }
    #skillsdiv div {
      margin: auto;
      width: 70%;
      box-shadow: rgba(73, 73, 73, 0.35) 0px 5px 15px;
      padding: 20px;
      text-align: center;
      border-radius: 3rem;
      border: 8px solid #37B9f1;
      box-shadow: #37B9f1 0px 3px 10px, #37B9f1 0px 4px 4px;
    }
  
    #skillsdiv div:hover {
      background-color: rgb(245, 202, 200);
      
    }
    #skillsdiv img {
      width: 71%;
  
    }
    
  
    #tools {
      width: 250px;
      margin: auto;
      font-size: 35px;
      margin-top: 20px;
      margin-bottom: 20px;
      color:var(--color-second)
    }
  
  .active {
    background-color:#F2F5F5;
    color:#37B9f1 ;
  }
  @media all and (min-width: 768px) and (max-width: 1115px) {
    #skillsdiv {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }
  }
  @media all and (min-width: 320px) and (max-width: 767px) {
    #skillsdiv {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 20px;
  
      width: 80%;
      margin: auto;
    }
    #skillsdiv img {
      width: 70%;
    }
    #skillsdiv div {
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
      padding: 15px;
      border-radius: 15px;
      text-align: center;
      border-radius: 3rem;
      border: 8px solid #37B9f1;
      box-shadow: #37B9f1 0px 3px 10px, #37B9f1 0px 4px 4px;
    }
    #skillsdiv div:hover {
      background-color: rgb(255, 213, 255);
    }
    #tech h1 {
      font-size: 42px;
  
      text-align: center;
    }
    #tech {
      margin: auto;
      border-radius: 15px;
      font-size: 45px;
      margin-bottom: 90px;
    }
    /* #tech {
      border:1px solid red;
      align-items: center;
    
      width: 300px;
      font-size: 22px;
    }
    #tech h1 {
      margin: auto;
    
      margin-left: 4%;
      margin-bottom: 30px;
    } */
    #tools {
      margin: auto;
      width: 200px;
      font-size: 30px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }
  
  #button {
    padding: 20px;
  }