#aboutdiv {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    font-size: 15px;
    width: 85%;
    margin: auto;
    color: var(--color-heading);
    gap: 50px;
  }

  #aboutdiv img {
      width: 90%;
    border-radius: 2rem;
  }
  /* #aboutdiv li {
    padding: 10px;
  }
   */
  @media all and (min-width: 320px) and (max-width: 1050px) {
    #aboutdiv {
      gap: 20px;
    
      width: 80%;
      grid-template-columns: repeat(1, 1fr);
    }
   
    #aboutdiv>:nth-child(2){
      text-align: center;
    }
    #aboutdiv li {
      margin-left: -15%;
      padding: 5px;
    }
    #aboutdiv img {
      width: 80%;
    border-radius: 2rem;
  }
  
  }