#contactform {
    /* display: grid; */
    /* grid-template-columns: repeat(2, 1fr); */
    width: 50%;
    margin: auto;
    border-radius: 1rem;
    /* margin-left: 100px; */
    color: white;
    /* background-color: var(--color-bg) */
    /* background-color:#f2f5f5; */
  }
  #contact{
    margin-bottom: 50px;
  }
  #tech {
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    margin: auto;
    border-radius: 15px;
    font-size: 35px;
    margin-top: -30px;
    margin-bottom: 30px;
    color:var(--color-second)
  }
  #contactform div{
      padding:10px
  }
  #contactform a {
    color: var(--color-bg);
  }
  #contactform > :nth-child(2) {
    background-color: white;
    color: var(--color-heading);
    width: 70%;
  }
  #contactform > :nth-child(1) > div {
    border: 1px solid #1095cf;
  }
  #contactform > div {
    align-items: center;
    height: auto;
    margin: auto;
    border-radius: 1rem;
    /* padding: 20px; */
  }
  #contactform input {
    font-size: 17px;
    font-weight: bold;
    /* padding: 7px; */
    width: 80%;
    border: 1px solid var(--color-bg);
    border-radius: 5px;
    /* border: 2px solid var(--color-bg) */
  }
  #btnform {
 margin-top: 20px;
  }
  #formsub {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    width: 70%;
    margin-top: 30px;
    border-radius: 3rem;
    border: 8px solid var(--color-bg);
    box-shadow: var(--color-bg) 0px 10px 20px, var(--color-bg) 0px 6px 6px;
    margin: auto;
    margin-top: 30px;
  }
  #contactlinkdiv {
    display: grid;
    /* grid-template-columns: repeat(1,110%); */
    /* justify-content: center;
    align-items: center; */
    margin-left: 500px;
    gap: 20px;
    /* border:1px solid red */
  }
  
  #contactlinkdiv>div>div{
    display: flex;
    justify-content: center;
    align-items: center;
  
    /* border:1px solid red; */
  }
  #contactlinkdiv button{
   margin-bottom: 20px;
  
    /* border:1px solid red; */
  }
  #contactlinkdiv>div {
    display: grid;
    border-radius: 1rem;
  width:100%;
  margin:auto;
  /* margin-left: 4%; */
    grid-template-columns: repeat(2,1fr);
    justify-content: space-evenly;
  background-color: white;
  color:var(--color-heading);
  /* margin-right: 100px; */
  /* text-align: center;
  padding: 5px 15px 5px 15px;
  border-radius: 1rem;
  border:1px solid rgb(43, 255, 0); */
  }
  #contactlinkdiv a {
    text-decoration: none;
  }
  
  #socialgit {
    display: flex;
    justify-content: space-around;
  }
  #socialgit a {
    font-size: 40px;
    color: rgb(58, 57, 57);
  }
  .fa-github:hover{
    
    color:black
  }
  .fa-linkedin:hover{
color: rgb(10, 102, 194);
  }
  @media all and (min-width: 767px) and (max-width: 1115px) {
    
  
    #contactform {
      width: 95%;
      grid-template-columns: repeat(2, 1fr);
    }
    #contactlinkdiv>div {
      grid-template-columns: repeat(1,1fr);
    }
  }
  @media all and (min-width: 320px) and (max-width: 767px) {
    #contactform {
      width: 95%;
      grid-template-columns: repeat(1, 1fr);
    }
    #contactlinkdiv>div {
      grid-template-columns: repeat(1,1fr);
    }
    #sociallinks {
      margin: auto;
      margin-top: 20px;
      display: flex;
      color: rgb(255, 255, 255);
      width: 40%;
    }
    #sociallinks a {
      font-size: 40px;
      color: rgb(255, 255, 255);
    }
    #contactform input {
      font-size: 17px;
      font-weight: bold;
      padding: 7px;
      width: 80%;
      border: 2px solid var(--color-bg);
  
      border-radius: 5px;
      /* border: 2px solid var(--color-bg) */
    }
    #formsub {
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
      width: 85%;
      margin-top: 30px;
      border-radius: 3rem;
      border: 8px solid var(--color-bg);
      box-shadow: var(--color-bg) 0px 10px 20px, var(--color-bg) 0px 6px 6px;
      margin: auto;
      font-size: 14px;
      margin-top: 30px;
    }
  }